@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800&display=swap);
.navbar-custom {
  background-color: #ffffff;
  padding: 10px 0;
  box-shadow: 0 3px 4px rgba(0, 0, 0, 0.1);
}
  
/* Logo */
.navbar-logo {
  height: 50px;
  width: auto;
  margin-left: 10px;
}

/* Navbar brand styles */
.navbar-brand-custom {
  color: #BF4F36 !important;
  font-size: 1.5rem;
  font-weight: bold;
  text-decoration: none !important;
}

.navbar-brand-custom:hover {
  color: #BF4F36;
  opacity: 0.8;
}

/* Center the search form */
.search-form {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 600px;
}

.search-input {
  height: 44px;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 4px 0 0 4px;
  width: 100%;
  max-width: 70%; /* Prevents button from getting larger than input */
  flex-grow: 1;
}

.search-button {
  height: 44px;
  padding: 10px 15px;
  font-size: 16px;
  background-color: #E74C3C;
  color: white;
  border: none;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
  transition: background-color 0.3s ease;
  max-width: 30%;
  flex-grow: 0;
}

.search-button:hover {
  background-color: #f01800;
}

/* Navbar links styles */
.navbar-link-custom {
  color: #000000;
  text-decoration: none !important;
  margin-right: 15px;
}

.navbar-link-custom:hover {
  color: #E74C3C;
  opacity: 0.8;
}

/* Logout button styles */
.logina {
  border: 1px solid #2B3A42;
  background-color: #2B3A42;
  color: white !important;
  font-weight: bold;
  border-radius: 10px;
}

/* Responsive adjustments */
@media (max-width: 991px) {
  .navbar-custom .container-fluid {
    padding: 0 15px;
  }

  .navbar-collapse {
    background-color: #ffffff;
    padding: 15px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }

  .navbar-nav {
    align-items: center;
  }

  .navbar-link-custom {
    margin: 5px 0;
  }

  .logina {
    margin-top: 10px;
    padding: 8px 15px !important;
  }

  .search-form {
    margin: 15px 0;
    max-width: 100%; /* Full width on small screens */
  }

  .search-input {
    width: 65%; /* Ensures proper width for input and button */
  }

  .search-button {
    width: 35%; /* Smaller button width to fit better */
  }

  .navbar-logo {
    height: 30px;
  }
}

.footer {
  background-color: #2B3A42;
  text-align: center;
  padding: 3rem 0;
  margin-top: auto;
  
}

.footer-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer-logo, .footer-links, .footer-social {
  margin-bottom: 1.5rem;
}

.footer-icons {
  display: flex;
  justify-content: center;
  grid-gap: 20px;
  gap: 20px;
}

.social-icon-link {
  transition: transform 0.3s ease, color 0.3s ease;
}

.social-icon-link:hover {
  transform: translateY(-3px);
  color: #4a90e2 !important;
}

.footer-links ul {
  padding: 0;
}

.footer-links li {
  margin-bottom: 0.75rem;
}

.hover-effect {
  transition: color 0.3s ease;
}

.hover-effect:hover {
  color: #4a90e2 !important;
}

#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.main-content {
  flex: 1 1;
}

@media (min-width: 768px) {
  .footer-content {
      flex-direction: row;
      justify-content: space-between;
      align-items: flex-start;
  }

  .footer-logo,
  .footer-links,
  .footer-social {
      margin-bottom: 0;
  }

  .footer-links {
      text-align: left;
  }
}
